import axios from "axios";
// pour scuriser notre cl API
const API_KEY =process.env.REACT_APP_OPENAI_API_KEY;

const API_URL = `https://generativelanguage.googleapis.com/v1/models/gemini-1.5-flash:generateContent?key=${API_KEY}`;
// A RETENIR : ce modle is the one -> gemini-1.5-flash
export const generateIdeaFromAI = async (secteur) => {
  console.log(`Le secteur est ${secteur}`);
  try {
    

    const response = await axios.post(
      API_URL,
      {
        contents: [
          {
            parts: [
              {
                text: `Donne-moi une ide de startup innovante et originale dans le secteur ${secteur}.`
              }
            ]
          }
        ]
      },
      {
        headers: {
          "Content-Type": "application/json",
        },
      }
    );

    console.log("Rponse de l'API Gemini :", response.data); 

    if (response.data && response.data.candidates) {
      return response.data.candidates[0]?.content?.parts[0]?.text || "Rponse vide de l'API";
    } else {
      console.error("Rponse invalide reue de l'API");
      return "Impossible de gnrer une ide, dsol !";
    }
  } catch (error) {
    console.error("Erreur avec Google Gemini :", error.response ? JSON.stringify(error.response.data, null, 2) : error.message);
    return "Impossible de gnrer une ide, dsol !";
  }
};
